'use client';

import { useState } from 'react';
import ReasoningPreview from './ReasoningPreview';

const code = `import {
  Reasoning,
  ReasoningContent,
  ReasoningTrigger,
} from "@/components/prompt-kit/reasoning"
import { useState } from "react"

const simulateMarkdownStream = async (
  setText: (text: string) => void,
  setIsStreaming: (streaming: boolean) => void
) => {
  const reasoning = \`# Solving: Square Root of 144

## Step 1: Problem Analysis
I need to find a number that, when **multiplied by itself**, equals 144.

## Step 2: Testing Values
- \\\`10² = 100\\\` ❌ (too small)
- \\\`13² = 169\\\` ❌ (too large) 
- \\\`12² = 144\\\` ✅ (perfect!)

## Step 3: Verification
\\\`\\\`\\\`
12 × 12 = 144 ✓
\\\`\\\`\\\`

> **Answer:** The square root of 144 is **12**.\`

  setIsStreaming(true)
  setText("")

  for (let i = 0; i <= reasoning.length; i++) {
    setText(reasoning.slice(0, i))
    await new Promise((resolve) => setTimeout(resolve, 20))
  }

  setIsStreaming(false)
}

export function ReasoningMarkdown() {
  const [reasoningText, setReasoningText] = useState("")
  const [isStreaming, setIsStreaming] = useState(false)

  const handleGenerateReasoning = () => {
    simulateMarkdownStream(setReasoningText, setIsStreaming)
  }

  return (
    <div className="flex w-full flex-col items-start gap-4">
      <button
        onClick={handleGenerateReasoning}
        disabled={isStreaming}
        className="px-4 py-2 text-sm font-medium rounded-md border"
      >
        {isStreaming ? "Thinking..." : "Generate Reasoning"}
      </button>

      <Reasoning isStreaming={isStreaming}>
        <ReasoningTrigger>Show AI reasoning</ReasoningTrigger>
        <ReasoningContent
          className="ml-2 border-l-2 border-l-slate-200 px-2 pb-1 dark:border-l-slate-700"
        >
          {reasoningText}
        </ReasoningContent>
      </Reasoning>
    </div>
  )
}`;

export function ReasoningSection() {
  const [activeTab, setActiveTab] = useState<'preview' | 'code'>('preview');
  const [copied, setCopied] = useState(false);

  const handleCopy = async () => {
    try {
      await navigator.clipboard.writeText(code);
      setCopied(true);
      setTimeout(() => setCopied(false), 2000);
    } catch (err) {
      console.error('Failed to copy:', err);
    }
  };

  return (
    <div className="rounded-xl border border-fd-border overflow-hidden bg-fd-card">
      <div className="flex items-center justify-between border-b border-fd-border bg-fd-muted/30">
        <div className="flex">
          <button
            onClick={() => setActiveTab('preview')}
            className={`px-5 py-3 text-sm font-medium transition-colors relative ${
              activeTab === 'preview'
                ? 'text-fd-foreground'
                : 'text-fd-muted-foreground hover:text-fd-foreground'
            }`}
          >
            Preview
            {activeTab === 'preview' && (
              <span className="absolute bottom-0 left-0 right-0 h-0.5 bg-fd-foreground" />
            )}
          </button>
          <button
            onClick={() => setActiveTab('code')}
            className={`px-5 py-3 text-sm font-medium transition-colors relative ${
              activeTab === 'code'
                ? 'text-fd-foreground'
                : 'text-fd-muted-foreground hover:text-fd-foreground'
            }`}
          >
            Code
            {activeTab === 'code' && (
              <span className="absolute bottom-0 left-0 right-0 h-0.5 bg-fd-foreground" />
            )}
          </button>
        </div>

        {activeTab === 'code' && (
          <button
            onClick={handleCopy}
            className="mr-3 p-1.5 rounded-md text-fd-muted-foreground hover:text-fd-foreground hover:bg-fd-muted/50 transition-colors"
            title={copied ? 'Copied!' : 'Copy Code'}
          >
            {copied ? (
              <svg width="18" height="18" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round">
                <path d="M20 6L9 17l-5-5" />
              </svg>
            ) : (
              <svg width="18" height="18" viewBox="0 0 24 24" fill="currentColor">
                <path d="M12.7587 2H16.2413C17.0463 1.99999 17.7106 1.99998 18.2518 2.04419C18.8139 2.09012 19.3306 2.18868 19.816 2.43597C20.5686 2.81947 21.1805 3.43139 21.564 4.18404C21.8113 4.66937 21.9099 5.18608 21.9558 5.74817C22 6.28936 22 6.95372 22 7.75868V11.2413C22 12.0463 22 12.7106 21.9558 13.2518C21.9099 13.8139 21.8113 14.3306 21.564 14.816C21.1805 15.5686 20.5686 16.1805 19.816 16.564C19.3306 16.8113 18.8139 16.9099 18.2518 16.9558C17.8906 16.9853 17.4745 16.9951 16.9984 16.9984C16.9951 17.4745 16.9853 17.8906 16.9558 18.2518C16.9099 18.8139 16.8113 19.3306 16.564 19.816C16.1805 20.5686 15.5686 21.1805 14.816 21.564C14.3306 21.8113 13.8139 21.9099 13.2518 21.9558C12.7106 22 12.0463 22 11.2413 22H7.75868C6.95372 22 6.28936 22 5.74818 21.9558C5.18608 21.9099 4.66937 21.8113 4.18404 21.564C3.43139 21.1805 2.81947 20.5686 2.43597 19.816C2.18868 19.3306 2.09012 18.8139 2.04419 18.2518C1.99998 17.7106 1.99999 17.0463 2 16.2413V12.7587C1.99999 11.9537 1.99998 11.2894 2.04419 10.7482C2.09012 10.1861 2.18868 9.66937 2.43597 9.18404C2.81947 8.43139 3.43139 7.81947 4.18404 7.43598C4.66937 7.18868 5.18608 7.09012 5.74817 7.04419C6.10939 7.01468 6.52548 7.00487 7.00162 7.00162C7.00487 6.52548 7.01468 6.10939 7.04419 5.74817C7.09012 5.18608 7.18868 4.66937 7.43598 4.18404C7.81947 3.43139 8.43139 2.81947 9.18404 2.43597C9.66937 2.18868 10.1861 2.09012 10.7482 2.04419C11.2894 1.99998 11.9537 1.99999 12.7587 2Z" />
              </svg>
            )}
          </button>
        )}
      </div>

      <div className="relative">
        {activeTab === 'preview' ? (
          <div className="p-8 flex items-center justify-center min-h-[200px] bg-fd-background">
            <ReasoningPreview />
          </div>
        ) : (
          <pre className="overflow-x-auto p-4 text-sm max-h-[500px] bg-fd-secondary/50 m-0">
            <code className="text-fd-secondary-foreground">{code}</code>
          </pre>
        )}
      </div>
    </div>
  );
}
